import $ from "./jquery.js";
import { tokens } from "./tokenDatasWithOwners.js";

var $address = $("#address");
var $validationButton = $("#validationButton");
var $resultList = $("#resultList");
var $resetButton = $("#resetButton");
var $error = $("#error");

$resetButton.click((e) => {
  $resultList.empty();
  $address.val("");
});

$validationButton.click((e) => {
  const testedOwner = $address.val();
  if (testedOwner) {
    $resultList.empty();
    $error.text("");
    tokens.forEach((tokenData) => {
      if (tokenData.owner === testedOwner) {
        $error.text("Owned by this address ");
        $resultList.append(
          `<li>Id : ${tokenData.token_id} | Rarity : ${tokenData.attributes[2].value}</li>`
        );
      }
    });
  }
  if ($("#resultList li").length === 0) {
    $error.text("Not an owner address.");
  }
});
